import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ApiService} from '../../api.service';
import {Router} from '@angular/router';
import {SharedServiceService} from '../../shared-service.service';

@Component({
  selector: 'products',
  templateUrl: './products.page.html',
  styleUrls: ['./products.page.scss'],
  encapsulation: ViewEncapsulation.None
})
export class ProductsPage implements OnInit {

  constructor(private sharedService: SharedServiceService, private adminService: ApiService, private router: Router) { }
  productList = [];
  ngOnInit() {
    this.getProducts();
  }

  ionViewWillEnter() {
    this.sharedService.showSearchBox.next(true);
  }

  ionViewWillLeave() {
    this.sharedService.showSearchBox.next(false);
  }
  getProducts() {
    this.adminService.getProducts().subscribe(
        res => this.getProductSuccess(res),
        error => {
          this.adminService.commonError(error);
        }
    );
  }

  getProductSuccess(res) {
    this.productList = res;
    this.productList = this.productList.map((v) => {
      v.showSpinner = false;
      return v;
    });
  }
}
